.tree-view-container {
  min-height: 100vh;
  width: 350px;
  background: rgb(0, 71, 110);
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  color: #fff;
}

.menu-list-container {
  list-style-type: none;
  margin-top: 0px;
  margin-bottom: 0px;
  padding-inline-start: 10px;
}

@media screen and ( max-width: 600px ) {
  .tree-view-container {
     width: 50%;
  }
}